export class Room {

    constructor(private id: number, private name: string, private pcolor?: string) {

    }
    get color(): string {
        return this.pcolor;
    }
    set color(color: string) {
        this.pcolor = color;
    }

    getName(): string {
        return this.name;
    }
    setName(name: string): void {
        this.name = name;
    }
    getId(): number {
        return this.id;
    }
    setId(id: number): void {
        this.id = id;
    }
}
