import { Injectable } from '@angular/core';
const ENV = 'DEV';
const DEV_API_URL = 'http://localhost:8080';
const TEST_API_URL = 'http://';
const PROD_API_URL = 'http://';

@Injectable({
  providedIn: 'root'
})
export class ConfigurationService {

  constructor() { }
  getApiBaseUrl(): string {
    if (ENV === 'DEV') {
      return DEV_API_URL;
    } else if (ENV === 'TEST') {
      return TEST_API_URL;
    } else if (ENV === 'PROD') {
      return PROD_API_URL;
    }

    return null;
  }

  getApiUrl(resource: string, operation: string): string {
    switch (resource) {
      case 'app-user':
        switch (operation) {
          case 'login':
            return this.getApiBaseUrl() + '/user/findByEMailOrPhoneNumber?email=${user}&password=${password}';
        }
        break;
      case 'registration':
        switch (operation) {
          case 'create':
            return this.getApiBaseUrl() + '/registration';
        }
        break;
    }
    return null;
  }

}
